/* chat */
.chat-answer {
    position: fixed;
    top: 60px;
    left: 0px;
    right: 0px;
    bottom: 90px;
    padding: 0 10px;
    display: flex;
    flex-direction: column;
    /* background: var(--main-glass-color); */
    /* backdrop-filter: var(--backdrop-filter); */
    animation: animate-popDown .3s ease-in;
}

.chat-answer-content {
    max-height: calc(100% - 20px - 20px);
    background: var(--main-bg-color-2);
    border-radius: var(--border-radius);
    padding: 10px;
    overflow: auto;
    font-size: var(--font-size-mid);
    box-shadow: var(--box-active-shadow);
}

.chat-ask-text {
    height: 40px;
    width: 100%;
    color: var(--primary-color);
    font-weight: bold;
    overflow-x: auto;
    overflow-y: hidden;
    white-space: nowrap;
    display: flex;
    align-items: center;
}

.chat-answer-text {
    width: 100%;
    height: calc(100% - 40px);
    overflow: auto;
    transition: 1s all;
}

/* feature */
.feature-list {
    width: calc(100% - 30px);
    padding: 0 15px;
    display: flex;
    flex-direction: column;
    gap: 20px;
    overflow: auto;
    padding-bottom: 100px;
}

.feature-item-row {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    gap: 10px;
}

.feature-mini-item {
    flex-shrink: 0;
    background: var(--main-bg-color-2);
    width: calc(100% - 5px);
    height: 100px;
    border-radius: var(--border-radius);
}

.feature-small-item {
    flex-shrink: 0;
    background: var(--main-bg-color-2);
    width: calc(50% - 5px);
    height: 100px;
    border-radius: var(--border-radius);
}

.feature-mid-item {
    flex-shrink: 0;
    background: var(--main-bg-color-2);
    width: 100%;
    height: 150px;
    border-radius: var(--border-radius);
}

.feature-larger-item {
    flex-shrink: 0;
    background: var(--main-bg-color-2);
    width: 100%;
    height: 300px;
    border-radius: var(--border-radius);
}

.feature-head {
    width: calc(100% - 20px);
    height: 40px;
    display: flex;
    padding: 0 10px;
}

.feature-head-title {
    width: 100px;
    height: 100%;
    display: flex;
    align-items: center;
    color: var(--primary-color);
    font-weight: bold;
}

.feature-head-operate {
    width: calc(100% - 100px);
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: right;
}

.feature-head-operate-btn {
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 5px;
}

.feature-content {
    width: clac(100% - 20px);
    height: calc(100% - 40px);
    padding: 0 10px;
}

/* chat-list */
.chat-record-head {
    width: calc(100% - 20px);
    height: 30px;
    line-height: 30px;
    display: flex;
    padding: 5px 10px;
}

.chat-record-head-title {
    width: calc(100% - 30px);
    font-weight: bold;
    color: var(--primary-color);
}

.chat-record-head-operate {
    width: 30px;
    color: var(--danger-color);
    cursor: pointer;
}

.chat-record-list {
    width: calc(100% - 20px);
    height: calc(100% - 40px - 20px);
    display: flex;
    align-items: center;
    margin: 10px;
    gap: 10px;
    color: white;
    overflow: auto;
}

.chat-record-list-item {
    flex-shrink: 0;
    height: 50px;
}

.chat-record-list-content {
    height: 100%;
    padding: 0 10px;
    background: var(--primary-color);
    border-radius: var(--border-radius);
    font-weight: bold;
    cursor: pointer;
}

.chat-record-list-content-del {
    color: red;
    margin-left: 10px;
}

/* habbit */
.habit-list {
    height: 100%;
    width: 100%;
    display: flex;
    flex-direction: row;
    overflow-x: auto;
}

.habit-item {
    position: relative;
    flex-shrink: 0;
    width: 100px;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.habit-item-ok {
    position: absolute;
    top: 5%;
    right: 30%;
    font-size: var(--font-size-big);
    text-align: center;
    line-height: 20px;
    color: var(--success-color);
    font-weight: bold;
    /* animation: animate-popDown .3s linear; */
}

.habit-item-icon {
    height: 50%;
    font-size: 50px;
}

.habit-item-time {
    font-size: var(--font-size-mini);
    color: var(--primary-color);
    font-weight: bold;
}

.habit-item-title {
    font-size: var(--font-size-small);
    font-weight: bold;
}

.habit-item-stat {
    font-size: var(--font-size-mini);
}